.footer{


    &__copyright{
        margin-top: 55px;
        border-top: 1px solid #eeeeee;
        padding: 40px 0 40px;
        padding-left: calc(((100% * 2 - 1500px ) / 2));
        position: relative;
        
        p,a{
            font-size: 13px;
            color: #808080;
            line-height: 1.2;
        }
        a{
            transition: color 0.3s ease-in;
            &:hover{
                color: var(--c-primary)
            }
        }
        span{
            margin: 0 6px;
        }
    }
}